.button__stripe {
  margin: 0;
}
.button__stripe__bar {
  background-color: #d4d4d4;
  position: relative;
  top: 3.3rem;
  z-index: 0;
  height: 1.6rem;
  width: 100%;
  pointer-events: visible;
}
.button__stripe__button {
  z-index: 1;
  position: relative;
  top: 1rem;
  text-align: center;
}
.button__stripe__button button {
  padding: 0.8rem 1.6rem;
}
.button__stripe__button button.disabled {
  background-color: #d4d4d4;
  opacity: 1;
  border: 0;
}
.button__stripe__button button.disabled:hover {
  background-color: #d4d4d4;
}
