.order__form__submit__line {
  margin: 0;
  margin-bottom: 1rem;
  text-align: center;
  position: sticky;
  bottom: 1rem;
}
.mobile__cart__view .order__form__submit__line {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.order__form__submit__line__strip {
  background-color: #d4d4d4;
  position: relative;
  top: 3.2rem;
  z-index: 0;
  height: 1.6rem;
  width: 100%;
}
.order__form__submit__line__button {
  margin: 1rem;
  padding: 0.8rem 1.6rem;
  z-index: 1;
  position: relative;
}
