.order__payment__method {
  margin: 0 0 1.4rem 0;
}
.order__payment__method span {
  margin-right: 0.5rem;
}
.order__payment__method button {
  position: relative;
  padding-top: 0.3rem;
  margin-bottom: 0.2rem;
  height: 2.8rem;
  font-variant-caps: all-petite-caps;
}
.order__payment__method button:first-of-type {
  margin-right: 0.8rem;
}
.order__payment__method span::before {
  margin-left: 0;
}
.order__payment__method__button__checked {
  display: absolute;
  left: 0;
  top: 0;
}
.order__payment__method__button__unchecked {
  width: 1.75rem;
  height: 1.4rem;
  margin: 0;
  display: inline-block;
}
