.address__input__box .input-group-addon {
  background: transparent;
  border: none;
  float: right;
}
.address__input__box .input-group-addon {
  background-color: transparent;
  color: #99230e;
}
.address__input__box .input-group-addon:hover {
  color: #b22810;
}
.address__input__box .flaticon-gps:before {
  margin-left: 0rem;
}
.address__input__box .input-group-btn {
  margin-left: 0.7rem;
}
.address__input .alert {
  margin-top: 0.3rem;
  border-radius: 1rem;
}
.address__input__box__resolved {
  margin: 0.4rem 0.7rem 0.4rem 0.7rem;
  font-size: 1rem;
  color: green;
  width: 100%;
}
.address__input__box__resolved.wait {
  display: flex;
  justify-content: center;
  font-size: 0.5rem;
  cursor: wait;
}
.address__input__hidden {
  display: none;
}
