a.category__navigator__scroll__link {
  text-align: center;
  color: #595959 !important;
  display: inline-block;
  min-width: 4rem;
  cursor: pointer;
  margin: 0 0.2rem;
}
a.category__navigator__scroll__link:hover,
a.category__navigator__scroll__link.active {
  color: #5ca651 !important;
  border-color: #5ca651;
}
.category__navigator__scroll__link__circle {
  background-color: white;
  border: 1px solid #595959;
  border-radius: 2rem;
  width: 2.6rem;
  height: 2.6rem;
  margin: 0 auto;
  padding: 0;
}
.active > .category__navigator__scroll__link__circle,
.category__navigator__scroll__link__circle:hover {
  border-color: #5ca651;
}
.category__navigator__scroll__link__circle span:before,
.category__navigator__scroll__link__circle span::after {
  margin: 0;
  padding: 0;
  font-size: 1.7rem;
}
.category__navigator__scroll__link__description {
  font-size: 0.9rem;
  margin-top: 0.2rem;
  max-width: 5rem;
}
