.finish__view {
  background-color: white;
  border-radius: 0.6rem;
  margin-top: 1rem;
}
.finish__view__header {
  color: white;
  border-radius: 0.5rem 0.5rem 0 0;
  padding: 0.9rem;
  position: relative;
}
.finish__view__header__inner {
  position: relative;
  display: block;
  padding: 0;
  white-space: nowrap;
  width: 17rem;
}
.finish__view__header h3 {
  display: inline-block;
  vertical-align: top;
  font-weight: normal;
  font-size: 1.4rem;
  position: absolute;
  left: 3.5rem;
}
.finish__view__header__icon::before {
  margin: 0;
  font-size: 2.7rem;
  position: absolute;
  left: 0rem;
  top: -1.1rem;
}
.finish__view__header__icon {
  display: inline-block;
  vertical-align: top;
  margin: 0;
  padding: 0;
}
.finish__view h4 {
  text-align: center;
  font-weight: normal;
  margin: 1.8rem 1rem 1.6rem 1rem;
}
.finish__view__items {
  margin: 1rem 4rem;
  border: 1px solid #ebebeb;
  border-radius: 0.5rem;
  padding: 1rem 1.4rem;
  position: relative;
  background-color: white;
  font-size: 0.9rem;
}
.finish__view__items__look__to {
  font-size: 1.1rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
.finish__view__items__price {
  position: absolute;
  right: 0;
  bottom: -1.4rem;
  min-width: 17rem;
  height: 2.4rem;
  padding: 0.3rem 0.9rem 0.3rem 0rem;
  font-weight: normal;
  font-size: 1.2rem;
  color: white;
  text-align: right;
}
.finish__view__items__price__corner {
  position: absolute;
  right: 16.99rem;
  bottom: -1.4rem;
  width: 0;
  height: 2.4rem;
  border-top: 2.4rem solid #54984a;
  border-left: 2.4rem solid transparent;
}

.finish__view__support {
  text-align: center;
  padding-bottom: 1rem;
}
.finish__view__support h4 {
  font-weight: normal;
  margin: 4rem 0 0 0;
}
.finish__view__support__icon::before {
  margin: 0 1rem 0 0;
  font-size: 1.8rem;
}
.finish__view__support__phone {
  font-size: 2rem;
  font-weight: 500;
}
.finish__view__apps__propagation {
  background-color: #99230e;
  color: white;
  min-height: 7rem;
  padding: 0.8rem 2rem 1.2rem 10rem;
  margin: 4rem 0 2rem 0;
}
.finish__view__apps__propagation__description {
  font-size: 0.8rem;
}
.finish__view__apps__propagation__button {
  margin: 1rem 1rem 0 0;
  color: black;
}

.finish__view__mobile {
  background-color: #f3f3f3;
}
.finish__view__mobile .finish__view__header {
  border-radius: 0;
  text-align: center;
}
.finish__view__mobile h4 {
  font-size: 1.2rem;
  margin: 1.5rem 1.5rem 1rem 1.5rem;
  text-align: center;
}
.finish__view__mobile .finish__view__header__inner {
  margin: 0 auto 0 auto;
}
.finish__view__mobile .finish__view__header__inner span::before {
  font-size: 2.2rem;
  margin: 0.2rem 0.6rem 0 0;
}
.finish__view__mobile .finish__view__header__inner h3 {
  font-size: 1.2rem;
  margin: 0.1rem 0 0 0;
}
.finish__view__mobile .finish__view__items {
  margin: 2rem 1rem 3rem 1rem;
}

.finish__view__submit__line {
  margin: 0;
}
.finish__view__submit__line__strip {
  background-color: #d5d5d5;
  position: relative;
  top: 3.2rem;
  z-index: 0;
  height: 1.6rem;
  width: 100%;
}
.finish__view__submit__line__button {
  margin: 1rem;
  padding: 0.8rem 1.6rem;
  z-index: 1;
  position: relative;
}
