.food__sidedish__picker {
  margin-top: 1rem;
}
.food__sidedish__picker__category {
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
  margin: 0 0 0.2rem 0.2rem;
}

@media (min-width: 900px) {
  .modal-dialog {
    max-width: 970px;
    margin: 2rem auto;
  }
}
@media (max-width: 370px) {
  .food__sidedish__picker button {
    width: 90%;
  }
}
