.cart__column {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  max-height: 100vh;
  overflow-y: auto;
}
.cart__items__container {
  margin: 0;
  padding: 0.5rem 0;
}

.cart__column__cart__empty__label {
  font-size: 1.4rem;
  display: inline-block;
  margin-top: 4rem;
  font-weight: lighter;
}
.cart__item__summary {
  width: 100%;
  min-height: 3rem;
  color: #1d1d1d;
  margin: 0.4rem 0 0.4rem 0;
  padding: 0.2rem;
  background: rgba(255, 255, 255, 0.8);
}
.cart__item__summary__total {
  background: rgba(15, 152, 73, 0.7);
  color: White;
  margin-top: 1.7rem;
}

.cart__item__summary__label {
  text-align: left;
  float: left;
  margin: 0.7rem 0 0 0.7rem;
}
.cart__item__summary__price {
  float: right;
  margin: 0.7rem 0.5rem 0 0;
  font-weight: 600;
}
